import React, { Component, useState } from 'react';
// import { Button, Image } from 'semantic-ui-react'
import '../assets/css/land.css'
import AOS from 'aos';
import 'aos/dist/aos.css';
import background from '../assets/img/background2.jpg'
import logo from '../assets/img/logo.png'
import phoneImg from '../assets/img/phone.png'
import featureImg from '../assets/img/Feature Graphic.png'
import app from '../assets/FuelFinder.apk'
let colours = {
    primary:'rgb(57, 105, 237)'
    // primary: '#0acd06' //39a54a
}

let IconTextBlock = ({ iconName, iconColour, header, text, component = null, fontSize = '14px', aos }) => {
    return (
        <div
            className={
                'textblock'
            }
            data-aos={(aos || '')}
        >
            {
                iconName ?
                    <ion-icon name={iconName}
                        style={{ color: (iconColour || 'Dodgerblue'), fontSize: '2em' }
                        } />
                    : component
            }
            {/* <i className={"fa " + iconName}
                style={{ color: (iconColour || 'Dodgerblue'), fontSize: '2em' }} /> */}
            <h4>{header}</h4>
            <div className='divText' style={{ textAlign: 'center', fontSize: fontSize }}>
                {text}
            </div>
        </div >
    )
}

let BrMin = ({ colour }) => {
    return (
        <div style={{ width: '50vw', height: '1px', margin: 'auto', marginTop: '5px', marginBottom: '5px', backgroundColor: (colour || 'grey') }}>        </div>
    )
}
let Br = ({ colour }) => {
    return (
        <div style={{ width: '90vw', height: '1px', margin: 'auto', marginTop: '10px', marginBottom: '10px', backgroundColor: (colour || 'grey') }}>        </div>
    )
}
let NumCircle = ({ colour, children }) => {
    return (
        <div style={{ backgroundColor: (colour || 'Dodgerblue'), borderRadius: '50%', width: '3em', height: '3em', display: 'flex', justifyContent: 'center', alignItems: 'center', color: 'white' }}>
            <h2 style={{ color: 'white' }}>{children}</h2>
        </div>
    )
}

let Button = ({ title, children }) => {
    return (
        <div style={{ border: '2px solid white', padding: '10px 20px', color: 'white' }}>
            {title || children}
        </div>
    )
}

let NavHeader = ({ logo, links }) => {

    let [dropdown, toggleDropdown] = useState(false)
    let [visible, setVisible] = useState(false)
    let toggle = () => {
        setVisible(!visible);
        toggleDropdown(!dropdown);
    }
    return (<div id='header'>
        <div className='main'>
            <img src={logo}></img>
            <ion-icon name="menu-sharp"
                onClick={toggle}
                style={{ fontSize: '3em', color: 'white' }}
            ></ion-icon>
        </div>
        <div className={'menuItems' + (dropdown ? ' visible' : ' hidden')} style={{ visibility: (visible ? '' : 'hidden') }}>
            {links.map(({ text, url },i) => {
                return (<a href={url} onClick={toggle} key={i}> { text} </a>)
            })}
        </div>

    </div >)
}
